    background-size: cover;
    height: 700px;
}

.contact_heading{
    text-align: center;
}

.contact_info{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    height: 50vh;
}

.contact_logo{
    height: 35px;
    width: 35px;
    border-radius: 50%;
}

.contact_main{
    display: grid;
    grid-template-columns: auto auto;
}

.contact_form{
    align-items: center;
}

.contact_form_elements{
    margin-top: 15px;
   width: 250px;
   height: 20px;
   border: none;
}

.submit{
    margin-top: 15px;
    background-color: red;
    border: none;
}

.submit :hover{
    background-color: blue;
}

input[type=text]:focus {
    background-color: lightcyan;
    
  }
